@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500;700&display=swap);
/* Typographic Scale: Major Third (1.250) */

/* Font Size Variables */
:root {
    --scale-factor: 1.250;
    --font-base: 14px;

    --scale-1: calc(var(--scale1) / var(--scale-factor));
    --scale1: 1em;
    --scale2: calc(var(--scale1) * var(--scale-factor));
    --scale3: calc(var(--scale2) * var(--scale-factor));
    --scale4: calc(var(--scale3) * var(--scale-factor));
    --scale5: calc(var(--scale4) * var(--scale-factor));
    --scale6: calc(var(--scale5) * var(--scale-factor));
}

/* Set default weight */
.text--small-body, .text--body, .text--big-body, .text--sub-heading, .text--heading, .text--sub-title, .text--title, .text--label {
    font-weight: 500;
}

/* Set line-heights */
.text--small-body, .text--body, .text--big-body {
    line-height: 1.6;
}
.text--sub-heading, .text--heading, .text--sub-title, .text--title , .text--label {
    line-height: 1.3;
}

/* Set font sizes */
.text--small-body, .text--label {
    font-size: calc(1em / 1.250);
    font-size: var(--scale-1);
}
.text--body, .input { 
    font-size: 1em; 
    font-size: var(--scale1);
}
.text--big-body {
    font-size: calc(1em * 1.250);
    font-size: var(--scale2);
}
.text--sub-heading {
    font-size: calc(calc(1em * 1.250) * 1.250);
    font-size: var(--scale3);
}
.text--heading {
    font-size: calc(calc(calc(1em * 1.250) * 1.250) * 1.250);
    font-size: var(--scale4);
}
.text--sub-title {
    font-size: calc(calc(calc(calc(1em * 1.250) * 1.250) * 1.250) * 1.250);
    font-size: var(--scale5);
}
.text--title {
    font-size: calc(calc(calc(calc(calc(1em * 1.250) * 1.250) * 1.250) * 1.250) * 1.250);
    font-size: var(--scale6);
}

/* Visual Utilities */
.text--label {
    font-weight: 700;
}
.text--bold, strong {
    font-weight: 700;
}
.text--italic, i {
    font-style: italic;
}
.text--strike {
    text-decoration: line-through;
}
.text--underline {
    text-decoration: underline;
}

/* Alignment Utilities */
.text--align-center {
    text-align: center;
}
.text--align-right {
    text-align: right;
}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

* { box-sizing: border-box; }
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
/* TopNav Component */

.topNav {
    position: relative;
    background: var(--gray-0);
    padding: 0 var(--su-50);
    display: grid;
    grid-template-columns: 1fr 4fr;
    align-items: center;
    box-shadow: 0px 4px 8px var(--gray-2);
    z-index: 99;
}
.topNav__menu {
    justify-content: right;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(0, -webkit-max-content));
    grid-template-columns: repeat(auto-fit, minmax(0, max-content));
}
.topNav__menu__item {
    font-size: var(--scale1);
    font-weight: 400;
    color: var(--gray-4);
    padding: var(--su-30) var(--su-20);
    text-decoration: none;
    transition: all 0.2s ease-in;
    -webkit-transition: all 0.2s ease-in;
    -moz-transition: all 0.2s ease-in;
    -ms-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
}
.topNav__menu__item:hover {
    color: var(--gray-5);
    background: var(--brand-light);
}
.topNav__menu__item__active {
    color: var(--gray-5);
    background: var(--brand-light);
    border-bottom: 2px solid var(--brand);
    transition: all 0.2s ease-in;
    -webkit-transition: all 0.2s ease-in;
    -moz-transition: all 0.2s ease-in;
    -ms-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
}
html, body { height: 100; }

body, input, select, textarea, button { font-family: 'Quicksand', sans-serif; font-size: var(--font-base); color: var(--gray-5); }
body { background: var(--gray-1); }
strong { font-weight: 700; }
